import { SemanticTriggerInterface } from './semantic-trigger.interface';

export interface SemanticTriggerRecordInterface
  extends SemanticTriggerInterface {
  id: string;
  tenantId: string;
}

export const SemanticTriggerIdentifier = (
  trigger: SemanticTriggerRecordInterface,
) => `${trigger.tenantId}_${trigger.eventType}`;
